<template>
  <section id="testimonials" class="testimonials">
    <div class="container">
      <div class="testimonials-slider swiper" data-aos="fade-up" data-aos-delay="100">
        <div class="swiper-wrapper">
          <div v-for="review in reviews" class="swiper-slide">
            <div class="testimonial-item">
              <img :src="review.image" class="testimonial-img" alt="">
              <h3>{{ review.name }}</h3>
              <h4>{{ review.description }}</h4>
              <p>
                <i class="bx bxs-quote-alt-left quote-icon-left"/>
                {{ review.review }}
                <i class="bx bxs-quote-alt-right quote-icon-right"/>
              </p>
            </div>
          </div><!-- End testimonial item -->
        </div>
        <div class="swiper-pagination"/>
      </div>
    </div>
  </section>
</template>

<script>
import 'boxicons/dist/boxicons'
import 'boxicons/css/boxicons.min.css'
import Swiper from 'swiper';
import 'swiper/swiper-bundle.css';
import {Pagination} from "swiper";

export default {
  name: "Review",
  data: () => {
    return {
      reviews: [
        {
          name: "Оксана",
          image: require("./assets/oxana.jpg"),
          description: "Клиентка",
          review: "Начала работать с агентством относительно недавно. Очень долго решалась сделать такой шаг, потому что не особо понимала, что это за работа. Благо ребята из Maximus все объяснили и показали. Действительно замечательное агентство, сотрудничеством очень довольна."
        },
        {
          name: "Натали",
          image: require("./assets/natalee.png"),
          description: "Самостоятельная клиентка",
          review: "Уже несколько лет сотрудничаю с Maximus. Сотрудничеством очень довольна, выплаты приходят без задержек."
        },
        {
          name: "Ангелина",
          image: require("./assets/IMG_177.jpg"),
          description: "Стример",
          review: "Очень благодарна агентству, за возможность проявить себя и начать зарабатывать."
        },
        {
          name: "Настя",
          image: require("./assets/IMG_056.jpg"),
          description: "Клиентка",
          review: "Отзывчивые и дружелюбные ребята. Помогли быстро влиться в коллектив и рабочий процесс."
        }
      ]
    }
  },
  mounted() {
    /**
     * Testimonials slider
     */
    Swiper.use([ Pagination]);
    new Swiper('.testimonials-slider', {
      modules: [Pagination],
      speed: 600,
      loop: true,
      autoplay: {
        delay: 5000,
        disableOnInteraction: false
      },
      slidesPerView: 'auto',
      pagination: {
        el: '.swiper-pagination',
        type: 'bullets',
        clickable: true
      }
    });
  }
}
</script>

<style scoped>

/*--------------------------------------------------------------
# Testimonials
--------------------------------------------------------------*/
.testimonials {
  padding: 80px 0;
  background: url("./assets/background.jpeg") no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
}
.testimonials::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(1, 3, 111, 0.8);
}
.testimonials .section-header {
  margin-bottom: 40px;
}
.testimonials .testimonials-carousel, .testimonials .testimonials-slider {
  overflow: hidden;
}
.testimonials .testimonial-item {
  text-align: center;
  color: #fff;
}
.testimonials .testimonial-item .testimonial-img {
  width: 100px;
  border-radius: 50%;
  border: 6px solid rgba(255, 255, 255, 0.15);
  margin: 0 auto;
}
.testimonials .testimonial-item h3 {
  font-size: 20px;
  font-weight: bold;
  margin: 10px 0 5px 0;
  color: #fff;
}
.testimonials .testimonial-item h4 {
  font-size: 14px;
  color: #ddd;
  margin: 0 0 15px 0;
}
.testimonials .testimonial-item .quote-icon-left, .testimonials .testimonial-item .quote-icon-right {
  color: rgba(255, 255, 255, 0.4);
  font-size: 26px;
}
.testimonials .testimonial-item .quote-icon-left {
  display: inline-block;
  left: -5px;
  position: relative;
}
.testimonials .testimonial-item .quote-icon-right {
  display: inline-block;
  right: -5px;
  position: relative;
  top: 10px;
}
.testimonials .testimonial-item p {
  font-style: italic;
  margin: 0 auto 15px auto;
  color: #eee;
}
.testimonials .swiper-pagination {
  margin-top: 20px;
  position: relative;
}
.testimonials .swiper-pagination .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  opacity: 1;
  background-color: rgba(255, 255, 255, 0.4);
}
.testimonials .swiper-pagination .swiper-pagination-bullet-active {
  background-color: #1acc8d;
}
@media (min-width: 1024px) {
  .testimonials {
    background-attachment: fixed;
  }
}
@media (min-width: 992px) {
  .testimonials .testimonial-item p {
    width: 80%;
  }
}
</style>