        },
        {
          question: "Как и с чего я буду зарабатывать?",
          answer: "Мы регистрируем Вас на сайтах наших партнеров, за Вами закрепляется переводчик, который и будет выполнять всю необходимую работу: вести чаты, отправлять фото и видео — каждый просмотр оплачивается! От Вас необходимо 1 раз в неделю присылать свои фото, видео и аудио материалы. И/или от 5 часов в неделю выходить совместно онлайн с переводчиком"
